import { State } from "./store";

export function saveToLocalStorage(state: State) {
    const jsonState = JSON.stringify(state)
    window.localStorage.setItem('state', jsonState)
}

export function loadFromLocalStorage(): State | undefined {

    try {

        const jsonState = window.localStorage.getItem('state')

        if (jsonState == null) {
            return undefined
        }

        // https://stackoverflow.com/a/24660721/1891491
        return JSON.parse(jsonState, function (name, value) {
            if (typeof value === "string" && /^\d\d\d\d-\d\d-\d\dT\d\d:\d\d:\d\d.\d\d\dZ$/.test(value)) {
                return new Date(value);
            }
            return value;
        })

    } catch (error) {
        console.error(error);
        return undefined
    }

}
