import { composeWithDevTools } from 'redux-devtools-extension'
import { createStore } from "redux";
import { List } from "../model/models";
import { AppActions } from "./actions";
import { ActionType, getType } from "typesafe-actions";
import * as uuid from 'uuid'
import { saveToLocalStorage, loadFromLocalStorage } from './local-storage';
import { updateList } from '../util/recur-engine';

export interface State {
    activeListId: string,
    lists: List[]
}

function createEmptyList(): List {

    const startFrom = new Date()

    return {
        id: uuid.v1(),
        createdAt: new Date(),
        modifiedAt: new Date(),
        listName: `untitled list`,
        items: [],
        recurs: {
            period: "weeks",
            length: 1,
            startFrom,
            previouslyRecurred: null,
        }
    }
}

let firstListUuid = uuid.v1()

const initState = (): State => {
    const startFrom = new Date()

    return {
        activeListId: firstListUuid,
        lists: [{
            id: firstListUuid,
            createdAt: new Date(),
            modifiedAt: new Date(),
            listName: `untitled list`,
            items: [],
            recurs: {
                period: "weeks",
                length: 1,
                startFrom,
                previouslyRecurred: null,
            }
        }]
    }
}

function rootReducer(
    state: State = initState(),
    action: ActionType<typeof AppActions>
): State {

    switch (action.type) {

        case (getType(AppActions.resetRecuringItems)): {

            const newLists = state.lists.map(list => {
                return updateList(list)
            })

            return {
                ...state,
                lists: newLists
            }
        }

        case (getType(AppActions.updateWhenListRecurs)): {
            const list: List = {
                ...state.lists.filter(list => list.id === action.payload.listId)[0],
                modifiedAt: new Date(),
                recurs: action.payload.recurs
            }

            return {
                ...state,
                lists: [
                    ...state.lists.filter(list => list.id !== action.payload.listId),
                    list
                ]
            }
        }

        case (getType(AppActions.changeActiveList)): {
            return {
                ...state,
                activeListId: action.payload.listId,
            }
        }

        case (getType(AppActions.deleteList)): {
            const prunedListsList = state.lists.filter(list => list.id !== action.payload.listId)
            const listIndex = state.lists.findIndex(list => list.id === action.payload.listId)

            let nextIndex = prunedListsList.length - 1

            if (listIndex <= nextIndex) {
                nextIndex = listIndex
            }

            return {
                ...state,
                lists: prunedListsList,
                activeListId: prunedListsList[nextIndex].id
            }
        }

        case (getType(AppActions.createNewList)): {

            const newList = createEmptyList()

            return {
                ...state,
                lists: [
                    ...state.lists,
                    newList
                ],
                activeListId: newList.id,
            }
        }

        case (getType(AppActions.addTodoItem)): {

            const list = state.lists.filter(list => list.id === action.payload.listId)[0]
            list.items.push(action.payload.item)

            return {
                ...state,
                lists: [
                    ...state.lists.filter(list => list.id !== action.payload.listId),
                    {
                        ...list,
                        items: [
                            ...list.items
                        ]
                    }
                ]
            }
        }

        case (getType(AppActions.deleteItem)): {
            const list = state.lists.filter(list => list.id === action.payload.listId)[0]

            return {
                ...state,
                lists: [
                    ...state.lists.filter(list => list.id !== action.payload.listId),
                    {
                        ...list,
                        items: [
                            ...list.items.filter(item => item.id !== action.payload.itemId)
                        ]
                    }
                ]
            }
        }

        case (getType(AppActions.updateItem)): {

            const list = state.lists.filter(list => list.id === action.payload.listId)[0]
            const updatedItem = action.payload.item

            return {
                ...state,
                lists: [
                    ...state.lists.filter(list => list.id !== action.payload.listId),
                    {
                        ...list,
                        items: [
                            ...list.items.filter(item => item.id !== updatedItem.id),
                            updatedItem,
                        ]
                    }
                ]
            }

        }

        case (getType(AppActions.renameList)): {

            const listToUpdate = state.lists.filter(list => list.id === action.payload.listId)[0]

            return {
                ...state,
                lists: [
                    ...state.lists.filter(list => list.id !== action.payload.listId),
                    {
                        ...listToUpdate,
                        listName: action.payload.name,
                    }
                ]
            }

        }

    }

    return state
}

export const store = createStore(
    rootReducer,
    loadFromLocalStorage(),
    composeWithDevTools()
)

store.subscribe(() => {
    saveToLocalStorage(store.getState())
})
