import { Recurs, List } from "../model/models";
import { differenceInMilliseconds, addMilliseconds, isAfter } from "date-fns";

function getPeriodLengthAsMilli(recurs: Recurs) {
    const HOUR_AS_MILLI = 3600000

    switch (recurs.period) {
        case "weeks": {
            return (recurs.length * 24 * 7) * HOUR_AS_MILLI
        }

        case "days": {
            return (recurs.length * 24) * HOUR_AS_MILLI
        }

        case "hours": {
            return recurs.length * HOUR_AS_MILLI
        }

        default: {
            throw new Error(`Unhandled recurs period: ${recurs.period}`)
        }
    }
}

export function calculateRecurs(recurs: Recurs, now?: Date): {
    hasRecurred: boolean,
    previousRecurredDate: Date,
    timeUntilRecurs: number,
} {

    const nowDate: Date = (now ? now : new Date())
    const periodLengthMs = getPeriodLengthAsMilli(recurs)

    const periodStartDate: Date = (recurs.previouslyRecurred)
        ? recurs.previouslyRecurred
        : recurs.startFrom

    const nextRecurDate = addMilliseconds(periodStartDate, periodLengthMs)

    if (isAfter(nextRecurDate, nowDate)) {
        return {
            hasRecurred: false,
            previousRecurredDate: periodStartDate,
            timeUntilRecurs: differenceInMilliseconds(nextRecurDate, nowDate),
        }
    }

    // At this point, we know we have recurred, what we need to do now
    // is figure out if we've skipped intervals and calculate the correct
    // value for previousRecurredDate
    let previousRecurredDate = nextRecurDate

    while (true) {
        const possiblyTheFuture = addMilliseconds(previousRecurredDate, periodLengthMs)

        // This means the next occurrance will happen in the future
        // So the current previousRecurredDate is the correct one
        if (isAfter(possiblyTheFuture, nowDate)) {
            break
        }

        // If it wasn't in the future, it's safe to increment our
        // date with one more period
        previousRecurredDate = addMilliseconds(previousRecurredDate, periodLengthMs)
    }

    return {
        hasRecurred: true,
        previousRecurredDate: previousRecurredDate,
        timeUntilRecurs: differenceInMilliseconds(nextRecurDate, nowDate),
    }
}

export function updateList(list: List) {
    const recursCalc = calculateRecurs(list.recurs)

    if (recursCalc.hasRecurred === false) {
        return list
    }

    const updatedList: List = {
        ...list,
        recurs: {
            ...list.recurs,
            previouslyRecurred: recursCalc.previousRecurredDate,
        },
        modifiedAt: new Date(),
        items: list.items.map(item => {
            return {
                ...item,
                isChecked: false,
                modifiedAt: new Date()
            }
        })
    }

    return updatedList
}
