import React, { Dispatch, Fragment } from "react";
import { List } from "../model/models";
import { ListGroup, ListGroupItem, Button } from "reactstrap";
import { ActionType } from "typesafe-actions";
import { AppActions } from "../redux/actions";
import { connect } from "react-redux";
import { format } from 'timeago.js';
import { addMilliseconds, lightFormat } from "date-fns";
import { calculateRecurs } from "../util/recur-engine";

function mapDispToProps(disp: Dispatch<ActionType<typeof AppActions>>) {
    return {
        createNewList: () =>
            disp(AppActions.createNewList()),

        changeActiveList: (listId: string) =>
            disp(AppActions.changeActiveList({ listId }))
    }
}

type Props = ReturnType<typeof mapDispToProps>
    & {
        activeListId: string,
        lists: List[]
    }

const TimeUntil: React.FC<{ list: List }> = props => {
    const resetDateTime = lightFormat(addMilliseconds(new Date(), calculateRecurs(props.list.recurs).timeUntilRecurs), "yyyy-MM-dd HH:mm")
    const resetsIn = format(addMilliseconds(new Date(), calculateRecurs(props.list.recurs).timeUntilRecurs))

    return (
        <small title={resetDateTime}>
            Resets {resetsIn}
        </small>
    )
}

const ListItemContent: React.FC<{ list: List }> = props => {
    const itemProgress = `${props.list.items.filter(item => item.isChecked).length + " of " + props.list.items.length} completed`

    return (
        <div className="d-flex justify-content-between align-items-center">
            <div className="d-flex flex-column w-100">
                <div className="mb-1 font-weight-bold">
                    {props.list.listName}
                </div>
                <div className="d-flex justify-content-between w-100">
                    <div className="small">
                        {itemProgress}
                    </div>
                    <TimeUntil list={props.list} />
                </div>
            </div>
        </div>
    )
}

class ListOfTodoLists extends React.Component<Props> {

    public constructor(props: Props) {
        super(props)

        this.handleChangeActiveList = this.handleChangeActiveList.bind(this)
    }

    private handleChangeActiveList(listId: string) {
        if (listId === this.props.activeListId) {
            return
        }

        this.props.changeActiveList(listId)
    }

    public render() {
        const lists = this.props.lists.sort((a, b) => {
            const aRecurs = calculateRecurs(a.recurs).timeUntilRecurs
            const bRecurs = calculateRecurs(b.recurs).timeUntilRecurs

            if (aRecurs === bRecurs) {
                return a.listName.localeCompare(b.listName)
            }

            return aRecurs - bRecurs
        })

        const completedLists = lists.filter(list => {
            return list.items.length > 0 && list.items.filter(item => item.isChecked).length === list.items.length
        })

        const unCompletedLists = lists.filter(list => {
            return list.items.length === 0 || list.items.filter(item => item.isChecked).length !== list.items.length
        })

        return (
            <Fragment>
                <div className="border">

                    <div className="px-3 pt-3 mb-3 d-flex justify-content-between align-items-center">
                        <h3 className="m-0">Lists</h3>
                        <Button size="sm" onClick={this.props.createNewList}>New</Button>
                    </div>

                    <ListGroup flush={true}>
                        {unCompletedLists.map(list => {
                            const isActive = list.id === this.props.activeListId

                            return (
                                <ListGroupItem
                                    key={list.id}
                                    tag="button"
                                    action
                                    active={isActive}
                                    onClick={() => this.handleChangeActiveList(list.id)}>

                                    <ListItemContent list={list} />
                                </ListGroupItem>
                            )
                        })}
                    </ListGroup>

                </div>

                {completedLists.length > 0 &&
                    <div className="mt-2 border">
                        <ListGroup flush={true}>
                            {completedLists.map(list => {
                                const isActive = list.id === this.props.activeListId

                                return (
                                    <ListGroupItem
                                        color="success"
                                        key={list.id}
                                        tag="button"
                                        action
                                        active={isActive}
                                        onClick={() => this.handleChangeActiveList(list.id)}>

                                        <ListItemContent list={list} />
                                    </ListGroupItem>
                                )
                            })}
                        </ListGroup>
                    </div>
                }
            </Fragment>

        )
    }
}

export default connect(null, mapDispToProps)(ListOfTodoLists)
