import React, { Fragment, Dispatch } from "react";
import { List, Item, Recurs } from "../model/models";
import { Input, CustomInput, Label, Button, Form, Row, Col, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { ActionType } from "typesafe-actions";
import { AppActions } from "../redux/actions";
import { connect } from "react-redux";
import * as uuid from "uuid"
import { State } from "../redux/store";
import { setHours, setMinutes, isFuture, startOfDay, setSeconds } from "date-fns";
import ReactDatePicker from "react-datepicker";

function mapStateToProps(state: State) {
    return {
        // Cannot delete last list
        disableDelete: state.lists.length === 1
    }
}

function mapDispToProps(disp: Dispatch<ActionType<typeof AppActions>>) {
    return {
        addTodoItem: (listId: string, item: Item) =>
            disp(AppActions.addTodoItem({ listId, item })),

        updateItem: (listId: string, item: Item) =>
            disp(AppActions.updateItem({ listId, item })),

        deleteItem: (listId: string, itemId: string) =>
            disp(AppActions.deleteItem({ listId, itemId })),

        renameList: (listId: string, name: string) =>
            disp(AppActions.renameList({ listId, name })),

        deleteList: (listId: string) =>
            disp(AppActions.deleteList({ listId })),

        updateWhenListRecurs: (listId: string, recurs: Recurs) =>
            disp(AppActions.updateWhenListRecurs({ listId, recurs }))
    }
}

type Props = ReturnType<typeof mapDispToProps>
    & ReturnType<typeof mapStateToProps>
    & { list: List }

class TodoList extends React.Component<Props, {
    newItemName: string,
    renamingList: boolean,
    newListName: string,
    showModify: boolean,
    deleteModalVisible: boolean,
}> {

    readonly state = {
        newItemName: "",
        renamingList: false,
        newListName: "",
        showModify: false,
        deleteModalVisible: false,
    }

    public constructor(props: Props) {
        super(props)

        this.handleCreateNewItem = this.handleCreateNewItem.bind(this)
        this.handleNewItemNameChanged = this.handleNewItemNameChanged.bind(this)
        this.handleItemCheckbox = this.handleItemCheckbox.bind(this)
        this.handleRenameListInput = this.handleRenameListInput.bind(this)
        this.toggleEdit = this.toggleEdit.bind(this)
        this.handleResetDateChanged = this.handleResetDateChanged.bind(this)
        this.handleResetTimeChanged = this.handleResetTimeChanged.bind(this)
        this.toggleDeleteModalVisiblity = this.toggleDeleteModalVisiblity.bind(this)
    }

    public handleRenameListInput(event: any) {
        this.setState({
            newListName: event.target.value
        })
    }


    public handleNewItemNameChanged(event: any) {
        this.setState({
            newItemName: event.target.value
        })
    }

    public handleCreateNewItem(event: any) {
        event.preventDefault()

        if (this.state.newItemName.trim() === "") {
            this.setState({
                newItemName: ""
            })

            return
        }

        this.props.addTodoItem(
            this.props.list.id,
            {
                id: uuid.v1(),
                createdAt: new Date(),
                modifiedAt: new Date(),
                itemText: this.state.newItemName,
                isChecked: false,
            }
        )

        this.setState({
            newItemName: "",
        })
    }

    public handleItemCheckbox(item: Item, checked: boolean) {
        this.props.updateItem(this.props.list.id, {
            ...item,
            isChecked: checked,
            modifiedAt: new Date(),
        })
    }

    private beginRenameList() {
        this.setState({
            newListName: this.props.list.listName,
            renamingList: true,
        })
    }

    private endRenameList() {
        if (this.state.newListName.trim() !== "") {
            this.props.renameList(this.props.list.id, this.state.newListName)
        }

        this.setState({
            newListName: "",
            renamingList: false,
        })
    }

    private handleResetPeriodChanged(event: React.ChangeEvent<HTMLInputElement>) {
        const { value } = event.target as any

        if (!value) {
            return
        }

        const listId = this.props.list.id

        const recurs: Recurs = {
            ...this.props.list.recurs,
            period: value,
            startFrom: new Date(),
            length: 1,
        }

        this.props.updateWhenListRecurs(listId, recurs)
    }

    private handleResetLengthChanged(event: any) {
        const { value } = event.target

        // TODO: Assert not less than 1
        if (!value || value < 1) {
            return
        }

        const listId = this.props.list.id

        const recurs: Recurs = {
            ...this.props.list.recurs,
            length: event.target.value,
        }

        this.props.updateWhenListRecurs(listId, recurs)
    }


    private toggleEdit() {
        this.setState((prevState) => {
            return {
                showModify: !prevState.showModify
            }
        })
    }

    private handleResetDateChanged(newDate: Date) {
        const { startFrom } = this.props.list.recurs

        const oldHours = startFrom.getHours()
        const oldMinutes = startFrom.getMinutes()

        const newStartFrom = setSeconds(setMinutes(setHours(newDate, oldHours), oldMinutes), 0)

        const { id } = this.props.list
        this.props.updateWhenListRecurs(id, {
            ...this.props.list.recurs,
            startFrom: newStartFrom
        })
    }

    private handleResetTimeChanged(newTime: Date) {
        if (isFuture(newTime)) {
            return
        }

        const { startFrom } = this.props.list.recurs

        const newHours = newTime.getHours()
        const newMinutes = newTime.getMinutes()

        const newStartFrom = setSeconds(setMinutes(setHours(startFrom, newHours), newMinutes), 0)

        const { id } = this.props.list
        this.props.updateWhenListRecurs(id, {
            ...this.props.list.recurs,
            startFrom: newStartFrom
        })
    }

    private toggleDeleteModalVisiblity() {
        this.setState((state) => {
            return {
                deleteModalVisible: !state.deleteModalVisible
            }
        })
    }

    public render() {

        const listItems = this.props.list.items.sort((a, b) => {
            return a.itemText.localeCompare(b.itemText)
        })

        const unCompletedItems = listItems.filter(item => item.isChecked === false)
        const completedItems = listItems.filter(item => item.isChecked)

        return (
            <Fragment>

                <Modal backdrop={"static"} fade={false} isOpen={this.state.deleteModalVisible} toggle={this.toggleDeleteModalVisiblity}>
                    <ModalHeader toggle={this.toggleDeleteModalVisiblity}>Confirm</ModalHeader>
                    <ModalBody>
                        You're about to remove a list that contains items. This action
                        cannot be undone. Do you want to continue?
                    </ModalBody>
                    <ModalFooter>
                        <Button color="danger" onClick={() => {
                            this.props.deleteList(this.props.list.id)
                            this.toggleDeleteModalVisiblity()
                        }}>Delete list</Button>
                        <Button color="secondary" onClick={this.toggleDeleteModalVisiblity}>Cancel</Button>
                    </ModalFooter>
                </Modal>

                {this.state.renamingList === false &&
                    <div className="mb-4 d-flex align-items-center">
                        <h2 className="w-100" style={{
                            cursor: "pointer"
                        }} onClick={() => {
                            this.beginRenameList()
                        }}>
                            {this.props.list.listName}
                        </h2>

                        <Button
                            disabled={this.props.disableDelete}
                            size="sm"
                            onClick={() => {
                                if (this.props.list.items.length > 0) {
                                    this.toggleDeleteModalVisiblity()
                                } else {
                                    this.props.deleteList(this.props.list.id)
                                }
                            }}>
                            Delete
                        </Button>
                    </div>
                }

                {this.state.renamingList &&
                    <div className="mb-4 d-flex flex-column">
                        {/* <p className="mb-2">Renaming list</p> */}
                        <Input
                            autoFocus={true}
                            value={this.state.newListName}
                            onChange={this.handleRenameListInput}
                            onKeyDown={(event: React.KeyboardEvent<HTMLInputElement>) => {
                                if (event.key === "Enter") {
                                    this.endRenameList()
                                }
                            }}
                            onBlur={() => {
                                this.endRenameList()
                            }}
                        />
                    </div>
                }

                <div
                    tabIndex={0}
                    className="p-2 pl-3 bg-light border"
                    style={{ cursor: "pointer" }}
                    onClick={this.toggleEdit}
                    onKeyPress={this.toggleEdit}
                >
                    Edit
                </div>

                {this.state.showModify &&
                    <div className="p-3 border border-top-0">
                        <Row>
                            <Col lg={4} className="mt-3 mt-lg-0">
                                <Label>Reset every</Label>
                                <Input
                                    type="number"
                                    min={1}
                                    value={this.props.list.recurs.length}
                                    onChange={(event) => {
                                        this.handleResetLengthChanged(event)
                                    }}
                                />
                            </Col>
                            <Col lg={8} className="mt-2 mt-lg-0 d-flex align-items-end">
                                <CustomInput
                                    type="select"
                                    id={`recurType-${this.props.list.id}`}
                                    value={this.props.list.recurs.period}
                                    onChange={(event) => {
                                        this.handleResetPeriodChanged(event)
                                    }}>
                                    <option value="weeks">Weeks</option>
                                    <option value="days">Days</option>
                                    <option value="hours">Hours</option>
                                </CustomInput>
                            </Col>

                            <Fragment>
                                <Col lg={6} className="mt-3 d-flex flex-column">
                                    <Label>Start date</Label>

                                    <ReactDatePicker
                                        className="form-control"
                                        selected={this.props.list.recurs.startFrom}
                                        maxDate={new Date()}
                                        dateFormat={['yyyy-MM-dd', 'yyyyMMdd']}
                                        strictParsing={true}
                                        onChange={(date) => {
                                            if (date === null) {
                                                return
                                            }

                                            this.handleResetDateChanged(date)
                                        }}
                                    />

                                </Col>
                                <Col lg={6} className="mt-3 d-flex flex-column">
                                    <Label>Start time</Label>

                                    <ReactDatePicker
                                        className="form-control"
                                        selected={this.props.list.recurs.startFrom}
                                        maxTime={new Date()}
                                        minTime={startOfDay(new Date())}
                                        timeCaption="Time"
                                        showTimeSelect={true}
                                        showTimeSelectOnly={true}
                                        timeIntervals={15}
                                        timeFormat={'HH:mm'}
                                        dateFormat={['HH:mm', 'HHmm']}
                                        strictParsing={true}
                                        onChange={(date) => {
                                            if (date === null) {
                                                return
                                            }

                                            this.handleResetTimeChanged(date)
                                        }}
                                    />
                                </Col>
                            </Fragment>
                        </Row>
                    </div>
                }

                <div className="mt-3">
                    <Form className="d-flex" onSubmit={this.handleCreateNewItem}>
                        <Input
                            type="text"
                            value={this.state.newItemName}
                            onChange={this.handleNewItemNameChanged}
                            placeholder={"Add new item..."}
                        />
                    </Form>
                </div>

                <div className="mt-3">
                    {listItems.length === 0 && (
                        <p>You have no items, create a new one!</p>
                    )}

                    <ul className="list-unstyled">
                        {unCompletedItems.map(item => {

                            return (
                                <li className="p-2 pl-3 mb-2 w-100 d-flex justify-content-between align-items-center border" key={item.id}>
                                    <div className="d-flex align-items-center">
                                        <CustomInput
                                            id={`cb-${item.id}`}
                                            type="checkbox"
                                            checked={item.isChecked}
                                            label={item.itemText}
                                            onChange={() => {
                                                this.handleItemCheckbox(item, !item.isChecked)
                                            }}
                                        />
                                    </div>

                                    <Button
                                        size="sm"
                                        color="link"
                                        onClick={() => this.props.deleteItem(this.props.list.id, item.id)}>
                                        Delete
                                    </Button>
                                </li>
                            )

                        })}
                    </ul>

                    {completedItems.length > 0 &&
                        <Fragment>
                            <hr />

                            <ul className="mt-3 list-unstyled">
                                {completedItems.map(item => {

                                    return (
                                        <li className="p-2 pl-3 mb-2 w-100 d-flex justify-content-between align-items-center border" key={item.id}>
                                            <div className="d-flex align-items-center">
                                                <CustomInput
                                                    id={`cb-${item.id}`}
                                                    type="checkbox"
                                                    checked={item.isChecked}
                                                    label={item.itemText}
                                                    onChange={() => {
                                                        this.handleItemCheckbox(item, !item.isChecked)
                                                    }}
                                                />
                                            </div>

                                            <Button
                                                size="sm"
                                                color="link"
                                                onClick={() => this.props.deleteItem(this.props.list.id, item.id)}>
                                                Delete
                                            </Button>
                                        </li>
                                    )

                                })}
                            </ul>
                        </Fragment>
                    }
                </div>
            </Fragment >
        )
    }
}

export default connect(mapStateToProps, mapDispToProps)(TodoList)
