import { createAction } from "typesafe-actions";
import { Item, Recurs } from "../model/models";

export const AppActions = {

    resetRecuringItems: createAction('resetRecuringItems'),

    addTodoItem: createAction('addTodoItem', action => {
        return (p: { listId: string, item: Item }) => action(p)
    }),

    updateItem: createAction('updateItem', action => {
        return (p: { listId: string, item: Item }) => action(p)
    }),

    deleteItem: createAction('deleteItem', action => {
        return (p: { listId: string, itemId: string }) => action(p)
    }),

    renameList: createAction('renameList', action => {
        return (p: { listId: string, name: string }) => action(p)
    }),

    updateWhenListRecurs: createAction('updateWhenListRecurs', a => {
        return (p: { listId: string, recurs: Recurs }) => a(p)
    }),

    createNewList: createAction('createNewList'),

    deleteList: createAction('deleteList', action => {
        return (p: { listId: string }) => action(p)
    }),

    changeActiveList: createAction('changeActiveList', action => {
        return (p: { listId: string }) => action(p)
    }),

}
